*{box-sizing:border-box}body{background:#444;color:white;height:100%;margin:0;font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;line-height:1.5}body#bg-img{background:url(../img/portback.jpg);background-attachment:fixed;background-size:cover}body#bg-img:after{content:'';position:absolute;top:0;right:0;width:100%;height:100%;background:rgba(68,68,68,0.9);z-index:-1}h1,h2,h3{margin:0;font-weight:400}h1.lg-heading,h2.lg-heading,h3.lg-heading{font-size:6rem}h1.sm-heading,h2.sm-heading,h3.sm-heading{margin-bottom:2rem;padding:0.2rem 1rem;background:rgba(73,73,73,0.5)}a{color:white;text-decoration:none}header{position:fixed;z-index:2;width:100%}.text-secondary{color:#eece1a}main{height:100%;padding:4rem}main .icons{margin-top:1rem}main .icons a{padding:0.4rem}main .icons a:hover{color:#eece1a;transition:all 0.5s ease-out}main#home{overflow:hidden}main#home h1{margin-top:20vh}
